@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom styles for Phase 2 Enhanced Content */
@layer base {
  body {
    font-family: 'Inter', system-ui, -apple-system, sans-serif;
  }
  
  /* Smooth scrolling for anchor links */
  html {
    scroll-behavior: smooth;
  }
}

@layer components {
  /* Enhanced hover animations */
  .hover-lift {
    @apply transition-all duration-300 ease-out;
  }
  
  .hover-lift:hover {
    @apply -translate-y-1 shadow-lg;
  }
  
  /* Gentle bounce animation */
  .hover-bounce {
    @apply transition-transform duration-300 ease-out;
  }
  
  .hover-bounce:hover {
    @apply -translate-y-0.5 scale-105;
  }
  
  /* Subtle scale animation */
  .hover-scale {
    @apply transition-transform duration-200 ease-out;
  }
  
  .hover-scale:hover {
    @apply scale-105;
  }
  
  /* Glow effect for interactive elements */
  .hover-glow {
    @apply transition-all duration-300 ease-out;
  }
  
  .hover-glow:hover {
    @apply shadow-lg shadow-primary/25;
  }
  
  /* Button styles */
  .btn-primary {
    @apply bg-primary text-white px-6 py-3 rounded-lg font-medium hover:bg-blue-700 hover:shadow-lg transition-all duration-200 transform hover:-translate-y-0.5;
  }
  
  .btn-secondary {
    @apply bg-transparent border-2 border-primary text-primary px-6 py-3 rounded-lg font-medium hover:bg-primary hover:text-white hover:shadow-lg transition-all duration-200 transform hover:-translate-y-0.5;
  }
  
  /* Card animations */
  .card-hover {
    @apply transition-all duration-300 ease-out hover:shadow-xl hover:-translate-y-1;
  }
  
  /* Fade in animations */
  .fade-in {
    animation: fadeIn 0.5s ease-in-out;
  }
  
  .fade-in-up {
    animation: fadeInUp 0.6s ease-out;
  }
  
  .fade-in-left {
    animation: fadeInLeft 0.6s ease-out;
  }
  
  .fade-in-right {
    animation: fadeInRight 0.6s ease-out;
  }
  
  /* Pulse animation for attention */
  .pulse-slow {
    animation: pulse 3s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  }
  
  /* Shimmer effect for loading states */
  .shimmer {
    background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
    background-size: 200% 100%;
    animation: shimmer 1.5s infinite;
  }
  
  /* Gradient backgrounds */
  .gradient-primary {
    background: linear-gradient(135deg, #2563eb 0%, #3b82f6 100%);
  }
  
  .gradient-secondary {
    background: linear-gradient(135deg, #059669 0%, #10b981 100%);
  }
  
  /* Interactive code blocks */
  .code-block-interactive {
    @apply border border-gray-200 hover:border-primary/30 transition-colors duration-300;
  }
  
  /* Enhanced focus states */
  .focus-ring {
    @apply focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2;
  }
  
  /* Tooltip styles */
  .tooltip {
    @apply absolute z-50 px-3 py-2 text-sm text-white bg-gray-900 rounded-lg shadow-lg opacity-0 pointer-events-none transition-opacity duration-200;
  }
  
  .tooltip-visible {
    @apply opacity-100 pointer-events-auto;
  }
  
  /* Loading spinner */
  .spinner {
    animation: spin 1s linear infinite;
  }
  
  /* Progress indicators */
  .progress-bar {
    @apply bg-gray-200 rounded-full overflow-hidden;
  }
  
  .progress-fill {
    @apply bg-primary h-full transition-all duration-500 ease-out;
  }
}

/* Custom animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Responsive typography improvements */
@layer base {
  h1 {
    @apply scroll-m-20 text-4xl font-extrabold tracking-tight lg:text-5xl;
  }
  
  h2 {
    @apply scroll-m-20 border-b pb-2 text-3xl font-semibold tracking-tight first:mt-0;
  }
  
  h3 {
    @apply scroll-m-20 text-2xl font-semibold tracking-tight;
  }
  
  h4 {
    @apply scroll-m-20 text-xl font-semibold tracking-tight;
  }
  
  p {
    @apply leading-7 [&:not(:first-child)]:mt-6;
  }
  
  blockquote {
    @apply mt-6 border-l-2 border-primary pl-6 italic;
  }
}

/* Dark mode support for enhanced UX */
@media (prefers-color-scheme: dark) {
  .tooltip {
    @apply bg-gray-800 text-gray-200;
  }
  
  .shimmer {
    background: linear-gradient(90deg, #374151 25%, #4B5563 50%, #374151 75%);
    background-size: 200% 100%;
  }
}

/* Accessibility improvements */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
  
  .hover-lift:hover,
  .hover-bounce:hover,
  .hover-scale:hover {
    transform: none !important;
  }
}

/* Screen reader only content */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/* Enhanced focus styles for keyboard users */
.focus-visible:focus-visible {
  @apply outline-none ring-2 ring-primary ring-offset-2;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .btn-primary {
    @apply border-2 border-gray-900;
  }
  
  .btn-secondary {
    @apply border-2 border-gray-900;
  }
  
  .card-hover {
    @apply border border-gray-900;
  }
}

/* Skip links for keyboard navigation */
.skip-link {
  @apply sr-only focus:not-sr-only focus:absolute focus:top-4 focus:left-4 focus:z-50 focus:px-4 focus:py-2 focus:bg-primary focus:text-white focus:rounded-lg focus:shadow-lg;
}

/* Print styles */
@media print {
  .no-print {
    display: none !important;
  }
  
  .btn-primary,
  .btn-secondary {
    @apply border border-gray-400 bg-white text-gray-900;
  }
  
  /* Print-friendly colors */
  * {
    color: black !important;
    background: white !important;
  }
  
  /* Remove shadows and effects for print */
  .hover-lift,
  .hover-bounce,
  .hover-scale,
  .hover-glow {
    box-shadow: none !important;
    transform: none !important;
  }
} 